import qs from 'qs';
// VueVue()vmmain.jsVue.use(httpInterceptor, app)
// Vue"this"
// installhttps://uviewui.com/components/vueuse.html
const install = (Vue, vm) => {
	// 
	Vue.prototype.$u.http.setConfig({
		baseUrl: 'https://psypapi.gycm.net.cn',
		showLoading: false, // loading
		loadingText: '~',
		loadingTime: 800,
		loadingMask: true,
	});
	
	// 
	Vue.prototype.$u.http.interceptor.request = (config) => {
		console.log(config);
		if(config.method === 'POST' || config.method === 'PUT' || config.method === 'DELETE'){
			config.header["content-type"] = 'application/x-www-form-urlencoded; charset=utf-8';
			config.data = qs.stringify(config.data);
		}
		// token
		// vuextokenuViewvuex
		// https://uviewui.com/components/globalVariable.html
		// config.header.token = vm.token;
		
		// uViewvuex$store.state
		// config.header.token = vm.$store.state.token;
		
		// tokenglobalDatagetApp().globalData
		// config.header.token = getApp().globalData.username;
		
		// tokenStorage
		// Storage
		const token = uni.getStorageSync(Vue.prototype.tokenName);
		if(token){
			config.header.Authorization = token;
		}else{
			console.log('-token')
		}
		
		// configreturn
		return config;
		// returnfalse
		// if(config.url == '/user/rest') return false; // 
	}
	
	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		if(res.code == 0) {
			// rescoderesult
			// res.resultthis.$u.post(url).then(res => {})thenres
			// originalDatatrue
			return res.data;
		} else if(res.code == 1002){
			vm.$u.toast(res.msg);
			return false;
		} else if(res.code == 2001){
			uni.showToast({
				icon: 'none',
				title: ''
			})
			//vm.$u.toast('');
			setTimeout(() => {
				// uView
				uni.reLaunch({
					url: "../index/home"
				})
			}, 1500)
			return false;
		} else if(res.code == 2003){
			vm.$u.toast('');
			setTimeout(() => {
				// uView
				uni.reLaunch({
					url: "../index/home"
				})
			}, 1500)
			return false;
		} else if(res.code == 2005){
			vm.$u.toast('');
			setTimeout(() => {
				// uView
				uni.reLaunch({
					url: "../index/home"
				})
			}, 1500)
			return false;
		} else if(res.code == 2006) {
			// 201token
			vm.$u.toast('');
			setTimeout(() => {
				// uView
				uni.reLaunch({
					url: "../index/home"
				})
			}, 1500)
			return false;
		} else if(res.code == 3001) {
			vm.$u.toast(res.msg);
			//vm.$u.toast('');
			return false;
		} else if(res.code == 4001) {
			vm.$u.toast(res.msg);
			// vm.$u.toast('');
			return false;
		} else {
			// falsePromisereject
			// this.$u.post(url).then().catch(res=>{})catchres
			return false;
		}
	}
}

export default {
	install
}